	width: 100%;
	height: 0px;
	overflow: hidden;
	background-color: #F5A623;
	border-radius: 4px;
	position: relative;
	-webkit-transform: translateY(5px);
	        transform: translateY(5px);
	opacity: 0;
	-webkit-transition: .3s;
	transition: .3s;
	visibility: hidden;
}

@media (min-width: 1024px){

	.page .form .input-wrapper .input-content .invaild-message{
		width: -webkit-max-content;
		width: -moz-max-content;
		width: max-content;
		height: 26px;
		height: visible;
		margin: 0;
		padding: 0px 8px;
		position: absolute;
		top: 50%;
		left: calc(100% + 15px);
		-webkit-transform: translateX(-5px) translateY(-50%);
		        transform: translateX(-5px) translateY(-50%)
	}
}

.page .form .input-wrapper .input-content .invaild-message i{
	/* arrow */
	color: #F5A623;
	position: absolute;
	top: -7px;
	left: 50%;
	font-size: 20px;
	color: #F5A623;
	-webkit-transform: rotate(90deg) translateX(-50%);
	        transform: rotate(90deg) translateX(-50%);
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}

@media (min-width: 1024px){

	.page .form .input-wrapper .input-content .invaild-message i{
		-webkit-transform: rotate(0deg) translateY(-50%);
		        transform: rotate(0deg) translateY(-50%);
