.header{
    margin-bottom: 40px
}

.header .decoration-line{
    width: 100%;
    max-width: 458px;
    margin: auto;
    height: 5px;
    background-color: #0275D8;
    -webkit-animation: strokeWidth 1s;
            animation: strokeWidth 1s;
}

@media (min-width: 1024px){

    .header .decoration-line{
        height: 8px
    }
}

.header .progress{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-align-items: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 234px;
    margin: auto;
    padding-top: 40px;
}

@media (min-width: 1024px){

    .header .progress{
        padding-top: 60px
    }
}

.header .progress .circle{
    width: 17.5px;
    height: 16px;
    border: 2px solid #0275D8;
    border-radius: 100%;
    z-index: 2;
    position: relative
}

.header .progress .circle .shape{
    width: 8px;
    height: 8px;
    background-color: #0275D8;
    border-radius: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.header .progress .progress-line{
    width: calc(33.33333% - 16px);
    height: 2px
}

.header .progress .progress-line span{
    display: block;
    width: 100%;
    height: 100%;
    background-color: #0275D8;
}

.header .progress .undone{
    border-color: #BEE1FF
}

.header .progress .undone .shape{
    background-color: white;
}

.header .progress .undone span{
    background-color: #BEE1FF;
}

.header .progress .done:before{
    font-family: FontAwesome;
    content: '\f058';
    color: #0275D8;
    position: absolute;
    left: -1px;
    top: -4px;
    font-size: 18px;
}

.header .progress .done .shape{
    display: none;
}

@-webkit-keyframes strokeWidth{
    from{
        width: 20px;
        margin: auto;
    }
    to{
        width: 100%;
        margin: auto;
    }
}

@keyframes strokeWidth{
    from{
        width: 20px;
        margin: auto;
    }
    to{
        width: 100%;
        margin: auto;
    }
}


